.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-content: center;
}
.QuestionType1 {
  height: 100vh;
}

.AnswerBoxes {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: center;
}
.AnswerBox {
  flex: 0 1 300px;
  padding: 1em;
  border: 1px solid grey;
  border-radius: 10px;
}
.AnswerBox span {
  display: inline-block;
  padding: 0.5em 0.75em;
  margin-right: 1em;
  border: 1px solid grey;
  border-radius: 10px;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
